/* ==== Color Variable ==== */
$orange__color: #ff8b07;
$white__color: #ffffff;
$red__color: #ff0000;
$gray__color: #808080;
$blue__color: #0483fa;
$black__color: #000000;
$teal__color: #00adad;
$paragraph__color: #444444;
$heading__color: #1d1d1d;


/* ==== Heading Mixin ==== */
@mixin heading__1($f_size: 65px, $f_weight: 800, $l_height: 75px, $color: $heading__color, $m_top: 0, $m_bottom: 0) {
    font-size: $f_size;
    font-weight: $f_weight;
    line-height: $l_height;
    margin: $m_top 0 $m_bottom;
    color: $color
}

@mixin heading__2($f_size: 45px, $f_weight: 800, $l_height: 55px, $color: $heading__color, $m_top: 0, $m_bottom: 0) {
    font-size: $f_size;
    font-weight: $f_weight;
    line-height: $l_height;
    margin: $m_top 0 $m_bottom;
    color: $color
}

@mixin heading__3($f_size: 35px, $f_weight: 700, $l_height: 45px, $color: $heading__color, $m_top: 0, $m_bottom: 0) {
    font-size: $f_size;
    font-weight: $f_weight;
    line-height: $l_height;
    margin: $m_top 0 $m_bottom;
    color: $color
}

@mixin heading__4($f_size: 26px, $f_weight: 700, $l_height: 35px, $color: $heading__color, $m_top: 0, $m_bottom: 0) {
    font-size: $f_size;
    font-weight: $f_weight;
    line-height: $l_height;
    margin: $m_top 0 $m_bottom;
    color: $color
}

@mixin heading__5($f_size: 22px, $f_weight: 600, $l_height: 30px, $color: $heading__color, $m_top: 0, $m_bottom: 0) {
    font-size: $f_size;
    font-weight: $f_weight;
    line-height: $l_height;
    margin: $m_top 0 $m_bottom;
    color: $color
}

@mixin heading__6($f_size: 18px, $f_weight: 600, $l_height: 28px, $color: $heading__color, $m_top: 0, $m_bottom: 0) {
    font-size: $f_size;
    font-weight: $f_weight;
    line-height: $l_height;
    margin: $m_top 0 $m_bottom;
    color: $color
}

/* ==== Paragraph Mixin ==== */
@mixin paragraph($f_size: 16px, $f_weight: 400, $l_height: 28px, $color: $paragraph__color, $m_top: 0, $m_bottom: 0) {
    font-size: $f_size;
    font-weight: $f_weight;
    line-height: $l_height;
    margin: $m_top 0 $m_bottom;
    color: $color
}


/* ==== button Mixin ==== */
@mixin btn($f_size: 16px, $f_weight: 500, $l_height: 28px, $color: $orange__color, $bg_color: $white, $margin: 0, $padding: 5px 25px) {
    color: $color;
    background-color: $bg_color;
    font-size: $f_size;
    font-weight: $f_weight;
    line-height: $l_height;
    margin: $margin;
    padding: $padding;
    transition: 0.4s;

    &:hover {
        background-color: $orange__color;
        color: $white_color;
    }
}



/* ==== Utilites Mixin ==== */
@mixin display_flex($display: flex, $a_items: center, $j_content: center) {
    display: $display;
    align-items: $a_items;
    justify-content: $j_content;
}

@mixin hover($bg_color: $orange_color, $color: $white_color) {
    background-color: $bg_color;
    color: $color;
    transition: 0.4s;
}

@mixin show_hide($dispaly: default, $visibility: visible, $transform: default) {
    display: $dispaly;
    visibility: $visibility;
    transform: $transform;
    transition: 0.4s;
}

@mixin after_before ($content: "", $display: absolute, $inset: 0, $top: default, $bottom: default, $right: default, $left: default, $bg_color: default, $z_index: default) {
    content: $content;
    display: $display;
    inset: $inset;
    top: $top;
    right: $right;
    bottom: $bottom;
    left: $left;
    background-color: $bg_color;
    z-index: $z_index;

}