#layout {
    padding: 10px;


    .side__bar {
        height: 100vh;
        background-color: $teal__color;
        border-right: 5px solid $white__color;
        width: 250px;
        border-radius: 10px;

        .logo {

            h4 {

                font-size: 1.5rem;
                margin: 20px;
                text-align: center;
                color: $white__color;
                font-weight: 800;
            }

            hr {
                color: $white__color;
                height: 3px;
            }
        }

        .menu {
            padding: 20px;
            display: flex;
            flex-direction: column;

            .menu__item {
                padding: 5px 15px;
                background-color: rgba($orange__color, 0.1);
                border: 1px solid $white__color;
                cursor: pointer;
                color: $white__color;
                text-decoration: none;
                font-size: 1rem;
                font-weight: 600;

                i {
                    font-size: 1.2rem;
                    margin: 0 15px;
                    color: $orange__color;
                }

                &.active,
                &:hover {
                    background-color: $white__color;
                    transition: 0.4s;
                    color: $orange__color;



                    i {
                        color: $teal__color
                    }

                    ;
                }
            }
        }
    }

    .content__box {
        width: calc(100% - 300px);

        .header__box {
            height: 10vh;
            display: flex;
            align-items: center;
            justify-content: flex-end;

            background-color: $teal__color;

            .header__content {
                margin: 0 15px;

                a {
                    color: $white__color;
                    text-decoration: none;
                    font-size: 1.2rem;
                    font-weight: 800;
                    text-transform: uppercase;
                    transition: 0.4s;
                    margin-left: 30px;

                    &:hover {
                        color: $orange__color;
                    }
                }

                .notification {
                    transition: 0.4s;
                    cursor: pointer;
                    font-size: 1.2rem;
                    color: $white__color !important;



                    &:hover {
                        color: $orange__color;

                        i {
                            color: $orange__color;
                        }
                    }
                }


            }
        }

        .body__box {
            height: 90vh;
            background-color: rgba($orange__color, 0.1);
        }
    }
}