#spinner {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: #000;

    .container {
        position: relative;
        display: flex;
        height: 100px;
        width: 100%;
        justify-content: center;
        align-items: center;
        -webkit-box-reflect: below 0 linear-gradient(transparent, transparent, #0005);

        .loader {
            position: absolute;
            width: 100px;
            height: 100px;
            border-radius: 50%;
            animation: animate 2s linear infinite;

            span {
                position: absolute;
                top: 10px;
                left: 10px;
                right: 10px;
                bottom: 10px;
                background-color: #000;
                border-radius: 50%;
                z-index: 1;
            }

            i {
                position: absolute;
                top: 0;
                left: 50%;
                transform: translate(-50%);
                width: 20px;
                height: 20px;
                background: #00fff9;
                border-radius: 50%;
                z-index: 10;
                box-shadow: 0 0 10px #00fff9,
                    0 0 20px #00fff9,
                    0 0 30px #00fff9,
                    0 0 40px #00fff9,
                    0 0 50px #00fff9,
                    0 0 60px #00fff9,
                    0 0 70px #00fff9,
                    0 0 80px #00fff9,
                    0 0 90px #00fff9,
                    0 0 100px #00fff9;
            }
        }

        .loader:nth-child(2),
        .loader:nth-child(4) {
            filter: hue-rotate(290deg);
            animation-delay: -1s;
        }

        .loader:nth-child(1)::before,
        .loader:nth-child(2)::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 50%;
            height: 100%;
            background: linear-gradient(to top, transparent, rgba(0, 255, 249, .4));
            background-size: 100px 180px;
            background-repeat: no-repeat;
            border-top-left-radius: 100px;
            border-bottom-left-radius: 100px;
        }

    }
}







@keyframes animate {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}